import React from 'react';

const Ficha = (props) => {
    // Funciones útiles
    const isUppercase = (ltr) => {
        return ltr === ltr.toUpperCase() ? ltr.toLowerCase()+'_upper.png' : ltr.toLowerCase()+'_lower.png';
    }

    // Retorno
    return (
        <div className="ficha">
            <img
                src={props.estado ? "./letras/"+isUppercase(props.letra): './letras/back.png'}
                alt={props.letra}
                onClick={props.handleClick}
            />
        </div>
    )
}

export default Ficha